// 引入axios请求工具
import axios from 'axios';

// 设置baseURL，切换环境的时候方便更改域名
const instance = axios.create({
    // baseURL: '/',
    // baseURL: 'http://172.25.78.147:8081', // api 张浩
    // baseURL: 'http://172.25.78.92:8081', // api 内测
    // baseURL: 'http://111.203.254.12:8281', // api 外网
    // baseURL: 'http://172.25.78.92:8081', // api 内测
    baseURL: 'http://111.203.254.12:8281', // api 外网
    // baseURL: 'http://172.25.117.10:8081', // api 客户内网
    timeout: 5000
})

// 全局请求拦截
// instance.interceptors.request.use(
//     config => {
//         config.headers.cb_token = sessionStorage.getItem('token') ? sessionStorage.getItem('token') : ""
//         return config;
//     },
//     err => {
        
//     }
// )

// 全局响应拦截
instance.interceptors.response.use(
    config => {
        return config;
    }
)

// 定义一个request请求方法
const request = (url, method, options = {}) => {
    return instance.request({
        url,
        method,
        data: options.data,
        params: options.params,
        responseType: options.responseType,
    })
}


// 将文件内的某些方法和变量公开
export const http = {
    request,
    get(url, options) {
        return request(url, 'GET', options)
    },
    post(url, options) {
        return request(url, 'POST', options)
    },
}