<template>
    <div class="map-area-container">
        <!-- <img src="@/assets/img/map.jpg" alt=""> -->
        <div id="map"></div>
        <HoverEcharts :echartsDataHover="echartsData"  ref="HoverEcharts" />
    </div>
</template>

<script>
import { http } from "@/utils/request";
import HoverEcharts from './HoverEcharts'
import L from "leaflet";
import "@supermap/iclient-leaflet";
import "../../styles/L.Icon.Pulse";
import myGreenIcon from '../../assets/img/green-icon.png'
import myYellowIcon from '../../assets/img/yellow-icon.png'
import myOrangeIcon from '../../assets/img/orange-icon.png'
import myRedIcon from '../../assets/img/red-icon.png'
import myPurpleIcon from '../../assets/img/purple-icon.png'
import myBrownIcon from '../../assets/img/brown-icon.png'
import myWarningIcon from '../../assets/img/warnint-icon.png'
import mapBg from '../../assets/img/map-bg.png'
// var host = window.isLocal ? window.server : "https://iserver.supermap.io";
// 客户
 var host = "http://172.25.117.10:8081";
var map,
    themeLayer,
    infoView,
    // baseUrl = host + "/iserver/services/map-china400/rest/maps/China_4326";

    //客户地图新
    baseUrl = host + "/geoesb/proxy/services/maps/rest/1b175309e7d842cf920db1bcb70c5e09/e23e48623d2d49898abd77c5b0144471";
    //客户地图旧
    // baseUrl = host + "/geoesb/proxy/services/maps/rest/b5ce8533f7ed4395a088535742771d82/982c56ca82fa431783ac18f157d5d5c4";
var themeData = [];
var resources = [];
var themeDataHover = []
var echartsData = null
var themeDataNew =[]
export default {
    components: {
        HoverEcharts
    },
    data() {
        return {
            themeDataNew,
            myIcon,
            echartsData,
            pulseIcon : L.icon.pulse({
            iconSize: [8, 10],
            color: '#2f8'
            }),
            resultLayer:null,
            markers:[]
        };
    },
    created() {
        
    },
    mounted() {
        this.getDataList();
        // this.initmap();

        
    },
    methods: {
        createLayers(result) {
        if (!result || !result || result.length < 1) {
            return;
        }
        result.map( (feature)=> {
            var latLng = L.CRS.EPSG3857.unproject(L.point(feature.lonLat));
            this.markers.push(L.marker(latLng, {icon: this.pulseIcon}));

        });
        this.resultLayer = L.featureGroup(this.markers).addTo(map);

    },
        // 地图数据
        getDataList() {
            let dataTime = this.$moment().subtract(1,'hours').format("YYYY-MM-DD HH:mm:ss");
            console.log(dataTime)
            let json = {
                username: "api",
                password: "a123456b",
                dataTime,
            };
            http.get(
                "https://api-dx-smart-city.airqualitychina.cn:9998/v1/dashboard/cmd/points",
                {
                    params: {
                        json,
                    },
                }
            ).then((res) => {
                themeData = res.data.result;
                themeData = themeData.map(item=>{
                    return {
                        lonLat:item.lng+ ',' +item.lat,
                        aqi:item.data[0].values[0].qc_value,
                        devsite_code:item.devsite_code+''
                    }
                })
                 
                themeDataNew = themeData
                themeDataNew = themeDataNew.map(item=>{
                        return {
                            lonLat:item.lonLat.split(','),
                            aqi:item.aqi,
                            devsite_code:item.devsite_code+''
                        }
                })
                themeDataNew.forEach(item=>{
                    item.lonLat = item.lonLat.map(item=>{
                        return parseFloat(item)
                    })
                })
                themeDataNew.forEach(item=>{
                    if(item.aqi<0){
                        item.aqi = '-'
                    }else{
                        item.aqi = parseInt(item.aqi)
                    }
                   
                })
                
                

                resources = [
                    {
                        text_currentCity:'ada '
                    }
                ]
                this.initmap();
                

            });
        },
        
        initmap() {
            // 检测是否支持 Canvas
            if (!document.createElement("canvas").getContext) {
                widgets.alert.showAlert(resources.msg_supportCanvas, false);
                return;
            }
            //var corner1 = L.latLng(39.7815, 116.2875);
            //var corner2 = L.latLng(39.63902 ,116.669661);

            var corner1 = L.latLng(39.44138536979536,116.20900742871117);
            var corner2 = L.latLng(39.83156398892147,116.72188973334208);
            var bounds = L.latLngBounds(corner1,corner2);

            map = L.map("map", {
                maxBounds:bounds,
                preferCanvas: true,
                // crs: L.CRS.EPSG3857,
                crs: L.CRS.EPSG4326 ,
                // center: [35, 106],
                // center: [39.72668, 116.34149],
                center: [39.66666, 116.48],
                maxZoom: 18,
                zoomSnap: 0.25,
                zoom: 10.5,
            });


            // 超图提供
            // var res = [0.0011883962612831169,9.375208845478092E-4,6.866455078125018E-4,5.149841308593764E-4,3.433227539062509E-4,2.5749206542968934E-4,1.7166137695312546E-4,1.2874603271484424E-4,8.583068847656273E-5,4.2915344238281474E-5,2.1457672119140737E-5,1.0728836059570355E-5,5.364418029785192E-6,2.6822090148926073E-6];
            // var ext = [[39.44138536979536,116.20900742871117],[39.83156398892147,116.72188973334208]];
            // map = L.map('map', {
            //     center: [ (ext[0][0]+ext[1][0])/2,(ext[0][1]+ext[1][1])/2],
            //     maxZoom: 13,
            //     zoom: 1,
            //     crs: L.Proj.CRS("EPSG:4326",{
            //         origin: L.point(ext[0][1], ext[1][0]),
            //         resolutions: res
            //     })
            // });
 
            
            //  L.supermap.cloudTileLayer(mapBg).addTo(map);
            //  this.createLayers(themeDataNew)

            themeDataNew.forEach(item=>{
                // <i class="iconfont" style="color:${positionColor};">&#xe6ed;</i>
                var lng = parseFloat(item.lonLat[0]);
                var lat = parseFloat(item.lonLat[1]);
                let positionColor;
                if((item.aqi>0&&item.aqi<=50)|| item.aqi == '-'){
                    positionColor = myGreenIcon
                }else if(item.aqi>50&&item.aqi<=100){
                    positionColor = myYellowIcon
                }else if(item.aqi>100&&item.aqi<=150){
                    positionColor = myOrangeIcon
                }else if(item.aqi>150&&item.aqi<=200){
                    positionColor = myRedIcon
                }else if(item.aqi>200&&item.aqi<=300){
                    positionColor = myPurpleIcon
                }else if(item.aqi>300){
                    positionColor = myBrownIcon
                }
                let content = `
                    <img style="width:36px; height:44px;" src="${positionColor}" />
                    <span style="color:#333; text-align:center; font-weight:bold; font-size:16px; width:25px;height:25px; line-height:25px; border-radius:10px; display: inline-block; background:#fff; position:relative;     top: -45px;
    left: 5px;">${item.aqi}</span>
                `
                var myDivIcon = L.divIcon({
                    html:content,
                    className:'my-div-icon',
                    iconSize: [18, 22],
                });
                var marker = L.marker([lat, lng], {icon: myDivIcon}).addTo(map);
                
                marker.on('click',async (e)=>{
                    if(e.latlng.lng == item.lonLat[0]&&e.latlng.lat == item.lonLat[1]){
                        let devsite_code = item.devsite_code
                        let res = await this.getHoverDataList(devsite_code)
                        this.echartsData = res.data.result[0]
                        this.$refs.HoverEcharts.init(this.echartsData)
                    }
                })
            })
            // 待反馈的标注
            let warningData = JSON.parse(sessionStorage.getItem('warningData'))
            if(warningData){
                warningData.newTableData.forEach(item=>{
                    var lng = parseFloat(item.wgs84_lng);
                    var lat = parseFloat(item.wgs84_lat);
                    let content = `
                        <img style="width:36px; height:44px;" src="${myWarningIcon}" />
                    `
                    var myDivIcon = L.divIcon({
                        html:content,
                        className:'my-div-icon',
                        iconSize: [18, 22],
                    });
                    let marker = L.marker([lat, lng], {icon: myDivIcon}).addTo(map);
                    marker.on('click',(e)=>{
                        this.$router.push({
                            name:'EarlyWarning',
                            query:{
                                details:{
                                    item,
                                    total:warningData.total,
                                    warningNumber:warningData.warningNumber,
                                    handleNumber:warningData.handleNumber,
                                },
                            }
                        })
                    })
                })
            }
            
            L.supermap.tiledMapLayer(baseUrl).addTo(map);



            


            // this.initThemeLayer();
            // 左下方图例
            // this.initLegendView();
            
        },
        initThemeLayer() {
                themeLayer = L.supermap
                    .labelThemeLayer("ThemeLayer")
                    .addTo(map);

                themeLayer.style = new SuperMap.ThemeStyle({
                    labelRect: true,
                    fontColor: "#000000",
                    fontWeight: "bolder",
                    fontSize: "18px",
                    fill: true,
                    fillColor: "#FFFFFF",
                    fillOpacity: 1,
                    stroke: false,
                    strokeColor: "#8B7B8B",
                });
                //用于专题图的属性字段名称
                themeLayer.themeField = "aqi";
                //风格数组，设定值对应的样式
                themeLayer.styleGroups = [
                    {
                        start: 0,
                        end: 51,
                        style: {
                            fillColor: "#6ACD06",
                            fontSize: "17px",
                        },
                    },
                    {
                        start: 51,
                        end: 101,
                        style: {
                            fillColor: "#FBD12A",
                            fontSize: "19px",
                        },
                    },
                    {
                        start: 101,
                        end: 151,
                        style: {
                            fillColor: "#FE8800",
                            fontSize: "22px",
                        },
                    },
                    {
                        start: 151,
                        end: 201,
                        style: {
                            fillColor: "#FF0000",
                            fontSize: "24px",
                        },
                    },
                    {
                        start: 201,
                        end: 301,
                        style: {
                            fillColor: "#CC0000",
                            fontSize: "26px",
                        },
                    },
                    {
                        start: 301,
                        end: 601,
                        style: {
                            fillColor: "#960453",
                            fontSize: "28px",
                        },
                    },
                ];
                themeLayer.on("click", this.handleMouseOver);

                this.addThemeFeatures();
        },

        async handleMouseOver(evt) {
                if (evt.target && evt.target.refDataID) {
                    var fea = themeLayer.getFeatureById(evt.target.refDataID);
                    if (fea) {
                        let devsite_code = fea.attributes.devsite_code
                        let res = await this.getHoverDataList(devsite_code)
                        this.echartsData = res.data.result[0]
                        // this.updateInfoView(fea);
                        this.$refs.HoverEcharts.init()
                    }
                } else if (infoView) {
                    infoView.remove();
                }
            },
        addThemeFeatures() {
                var labelFeatures = [];
                var feat;
                for (var i = 0; i < themeData.length; i++) {
                    var lonlat = themeData[i].lonLat.split(",");
                    var lng = parseFloat(lonlat[0]);
                    var lat = parseFloat(lonlat[1]);
                    var text = themeData[i].aqi;
                    feat = L.supermap.themeFeature(
                        [lat, lng, text],
                        themeData[i]
                    );
                    labelFeatures.push(feat);
                }
                themeLayer.addFeatures(labelFeatures);
        },
        //图层信息框
        updateInfoView(feature) {
                if (!feature && infoView) {
                    infoView.remove();
                    return;
                }
                if (!infoView) {
                    infoView = L.popup();
                }

                var statisticsData = this.getStatisticsData();
                var contentHTML =
                    "<table><tbody><tr>" +
                    "<td><div style='margin-left: 15px'>" +
                    "<table><tbody><tr>" +
                    "<td><div id='contentID' style='margin-top: 2px;margin-bottom: 2px;width:65px;text-align: center;font-size:35px;color: #ffffff;'>" +
                    feature.attributes.aqi +
                    "</div></td>" +
                    "<td style='padding-right: 20px;'></td>" +
                    "<td>" +
                    "<div id='textID' style='text-align: left;font-size: 19px;text-shadow: 1px 1px 0 #ffffff;'>" +
                    feature.attributes.quality +
                    "</div>" +
                    "<div style='font-size:10px;width:150px;'>" +
                    feature.attributes.time_point +
                    "</div>" +
                    "<div style=' font-weight:lighter; font-size:14px;'>" +
                    resources.text_currentCity +
                    "：" +
                    feature.attributes.area +
                    "</div>" +
                    "</td>" +
                    "</tr> </tbody></table>" +
                    "<table style='width:100%;font-size: 10px;border: 0 solid black;padding: 0;margin: 0;border-spacing: 0;'>" +
                    "<tbody>" +
                    "<tr>" +
                    "<td style='font-weight:bold;width:45px;'></td><td style='font-weight:bold;width:51px;' align='center' nowrap='true'>Current</td>" +
                    "<td  style=' font-weight:bold;width:49px;' align='center' nowrap='true'>Min</td>" +
                    "<td style='font-weight:bold;width:49px;' align='center' nowrap='true'>Max</td> " +
                    "</tr>" +
                    "<tr style='height: 23px;'><td style='padding-left: 0;padding-right: 0'><div style='width:46px;'><span style='font-weight:bold;'>PM2.5</span></div></td>" +
                    "<td style='font-size:11px;' align='center'>" +
                    feature.attributes.pm2_5 +
                    "</td>" +
                    "<td style='color:#0086c8;font-size:11px;' align='center'>" +
                    statisticsData.minNum +
                    "</td>" +
                    "<td  style='color:#ce3c3a;font-size:11px;' align='center'>" +
                    statisticsData.maxNum +
                    "</td>" +
                    "</tr>" +
                    "<tr style='height: 23px;'><td style='padding-left: 0;padding-right: 0'><div style='width:46px;'><span style='font-weight:bold;'>PM10</span></div></td>" +
                    "<td style='font-size:11px;' align='center'>" +
                    feature.attributes.pm10 +
                    "</td>" +
                    "<td style='color:#0086c8;font-size:11px;' align='center'>" +
                    statisticsData.minpm10 +
                    "</td>" +
                    "<td  style='color:#ce3c3a;font-size:11px;' align='center'>" +
                    statisticsData.maxpm10 +
                    "</td>" +
                    "</tr>" +
                    "<tr style='height:23px;'><td style='padding-left: 0;padding-right: 0'><div style='width:46px;'><span style='font-weight:bold;'>O3</span></div></td>" +
                    "<td style='font-size:11px;' align='center'>" +
                    feature.attributes.o3 +
                    "</td>" +
                    "<td style='color:#0086c8;font-size:11px;' align='center'>" +
                    statisticsData.minO3 +
                    "</td>" +
                    "<td style='color:#ce3c3a;font-size:11px;' align='center'>" +
                    statisticsData.maxO3 +
                    "</td></td>" +
                    "</tr>" +
                    "<tr style='height: 23px;'><td style='padding-left: 0;padding-right: 0'><div style='width:46px;'><span style='font-weight:bold;'>SO2</span></div></td>" +
                    "<td style='font-size:11px;' align='center'>" +
                    feature.attributes.so2 +
                    "</td>" +
                    "<td style='color:#0086c8;font-size:11px;' align='center'>" +
                    statisticsData.minSO2 +
                    "</td>" +
                    "<td  style='color:#ce3c3a;font-size:11px;' align='center'>" +
                    statisticsData.maxSO2 +
                    "</td></td>" +
                    "</tr>" +
                    "<tr style='height: 23px;'><td style='padding-left: 0;padding-right: 0'><div style='width:46px;'><span style='font-weight:bold;'>NO2</span></div></td>" +
                    "<td style='font-size:11px;' align='center'>" +
                    feature.attributes.no2 +
                    "</td>" +
                    "<td style='color:#0086c8;font-size:11px;' align='center'>" +
                    statisticsData.minNO2 +
                    "</td>" +
                    "<td  style='color:#ce3c3a;font-size:11px;' align='center'>" +
                    statisticsData.maxNO2 +
                    "</td></td>" +
                    "</tr></tbody> </table></div></td></tr></tbody> </table>";

                var latLng = getLatLng(feature.attributes.lonLat);
                infoView.setLatLng(latLng).setContent(contentHTML).addTo(map);

                //设置弹框内容颜色
                var groups = themeLayer.styleGroups;
                for (var i = 0; i < groups.length; i++) {
                    if (
                        feature.attributes.aqi >= groups[0].start &&
                        feature.attributes.aqi < groups[0].end
                    ) {
                        setColor("#6ACD06");
                    } else if (
                        feature.attributes.aqi >= groups[1].start &&
                        feature.attributes.aqi < groups[1].end
                    ) {
                        setColor("#FBD12A");
                    } else if (
                        feature.attributes.aqi >= groups[2].start &&
                        feature.attributes.aqi < groups[2].end
                    ) {
                        setColor("#FE8800");
                    } else if (
                        feature.attributes.aqi >= groups[3].start &&
                        feature.attributes.aqi < groups[3].end
                    ) {
                        setColor("#FF0000");
                    } else if (
                        feature.attributes.aqi >= groups[4].start &&
                        feature.attributes.aqi < groups[4].end
                    ) {
                        setColor("#CC0000");
                    } else if (
                        feature.attributes.aqi >= groups[5].start &&
                        feature.attributes.aqi < groups[5].end
                    ) {
                        setColor("#960453");
                    }
                }

                function setColor(color) {
                    document.getElementById("contentID").style.backgroundColor =
                        color;
                    document.getElementById("textID").style.color = color;
                }

                function getLatLng(latLng) {
                    var latLng = latLng.split(",");
                    return L.latLng(
                        parseFloat(latLng[1]),
                        parseFloat(latLng[0])
                    );
                }
        },
        //左下方图例
        initLegendView() {
            var legendView = L.control({ position: "bottomleft" });
            legendView.onAdd = ()=> {
                this._div = L.DomUtil.create("div", "panel legend");
                this._div.innerHTML =
                    "<div class='panel-body'><table>" +
                    "<tr class='text-center'><td>" +
                    resources.text_great +
                    "</td><td >" +
                    resources.text_good +
                    "</td> <td >" +
                    resources.text_mildPollution +
                    "</td><td>" +
                    resources.text_moderatePollution +
                    "</td><td >" +
                    resources.text_heavyPollution +
                    "</td><td >" +
                    resources.text_severePollution +
                    "</td></tr>" +
                    "<tr class='text-center'>" +
                    "<td style='background: #6ACD06'></td><td style='background: #FBD12A'></td><td  style='background: #FE8800'></td>" +
                    "<td style='background: #FF0000'></td><td style='background: #CC0000'></td><td  style='background: #960453'></td>" +
                    "</tr>" +
                    "<tr class='text-center'><td>0-51</td><td >51-101</td><td>101-151</td><td>151-201</td><td>201-301</td><td>301-601</td></tr>" +
                    "</table></div>";
                this.handleMapEvent(this._div, this._map);
                return this._div;
            };
            legendView.addTo(map);
        },
        // 地图数据鼠标划过接口请求
        getHoverDataList(devsite_code) {
            // http://172.25.116.154:8010/maintain/device_qc_data?json=%7B%22token%22:%22qiYrxXEv5mJPW6-dU5_wQdzC4D1uoYh9jAo4ID1cRGnaDlrgUxAkVwpEpJ3ZjcBf4JbJ9HzT_pVp_SzBR-6ubey5gp_nLFpGMCJVG87_UhJmNLq547Sr4awrLmR91rrCDayhWWTehaS0_CgvQFFzaePlXI2R4am8jv9TNg_z1Qs6XO7jICeRjFU67jZ_yEjfi29LCWhhgQGlI-fCZg2dDZSQ5tbRFDRb7BVu1dCuEgU%3D%22,%22devsiteCodes%22:%22116874%22,%22polType%22:%22134004,134002,121004,121026,121005,105024,199054,134001%22,%22startTime%22:%222021-12-28+12:00:00%22,%22endTime%22:%222021-12-28+12:00:00%22,%22timeType%22:%221h%22%7D
            return http.get(
                "https://api-dx-smart-city.airqualitychina.cn:9998/v1/dashboard/cmd/pointdatas",
                // "http://172.25.116.154:8010/maintain/device_qc_data?json=%7B%22token%22:%22qiYrxXEv5mJPW6-dU5_wQdzC4D1uoYh9jAo4ID1cRGnaDlrgUxAkVwpEpJ3ZjcBf4JbJ9HzT_pVp_SzBR-6ubey5gp_nLFpGMCJVG87_UhJmNLq547Sr4awrLmR91rrCDayhWWTehaS0_CgvQFFzaePlXI2R4am8jv9TNg_z1Qs6XO7jICeRjFU67jZ_yEjfi29LCWhhgQGlI-fCZg2dDZSQ5tbRFDRb7BVu1dCuEgU%3D%22,%22devsiteCodes%22:%22116874%22,%22polType%22:%22134004,134002,121004,121026,121005,105024,199054,134001%22,%22startTime%22:%222021-12-28+12:00:00%22,%22endTime%22:%222021-12-28+12:00:00%22,%22timeType%22:%221h%22%7D",
                {
                    params: {
                        json:{
                username: "api",
                password: "a123456b",
                devsite_code: devsite_code,
            },
                    },
                }
            )
            // .then((res) => {
            //      themeDataHover = res.data.result;
            // });
        },
        handleMapEvent(div, map) {
                if (!div || !map) {
                    return;
                }
                div.addEventListener("mouseover", function () {
                    map.dragging.disable();
                    map.scrollWheelZoom.disable();
                    map.doubleClickZoom.disable();
                });
                div.addEventListener("mouseout", function () {
                    map.dragging.enable();
                    map.scrollWheelZoom.enable();
                    map.doubleClickZoom.enable();
                });
            },
        getStatisticsData() {
                var statisticsDataHover;
                if (statisticsDataHover) {
                    return statisticsDataHover;
                }

                //遍历数组，获取单个属性。组成新的数组
                var pm25 = [],
                    pm10s = [],
                    o3s = [],
                    so2s = [],
                    no2s = [];
                for (var i = 0; i < themeDataHover.length; i++) {
                    pm10s.push(themeDataHover[i].pm2_5);
                    pm25.push(themeDataHover[i].pm10);
                    o3s.push(themeDataHover[i].o3);
                    so2s.push(themeDataHover[i].so2);
                    no2s.push(themeDataHover[i].no2);
                }
                //获取单个属性的最大最小值
                statisticsDataHover = {
                    maxNum: Math.max.apply(Math, pm25),
                    minNum: Math.min.apply(Math, pm25),
                    maxpm10: Math.max.apply(Math, pm10s),
                    minpm10: Math.min.apply(Math, pm10s),
                    maxO3: Math.max.apply(Math, o3s),
                    minO3: Math.min.apply(Math, o3s),
                    maxSO2: Math.max.apply(Math, so2s),
                    minSO2: Math.min.apply(Math, so2s),
                    maxNO2: Math.max.apply(Math, no2s),
                    minNO2: Math.min.apply(Math, no2s),
                };
                return statisticsDataHover;
            }
    },
};
</script>

<style lang="scss">

