<template>
    <div>
        <div class="title">空气质量实时情况</div>
        <div class="topbox">
            <div class="l_img">
                <template v-if="aqiLevel == '优' ">
                    <span class="text-green">{{aqiNumber}}</span>
                    <i class="text-green">{{aqiNumber}}</i>
                </template>
                <template v-else-if="aqiLevel == '良' ">
                    <span class="text-yellow">{{aqiNumber}}</span>
                    <i class="text-yellow">{{aqiNumber}}</i>
                </template>
                <template v-else-if="aqiLevel == '轻度污染' ">
                    <span class="text-orange">{{aqiNumber}}</span>
                    <i class="text-orange">{{aqiNumber}}</i>
                </template>
                <template v-else-if="aqiLevel == '中度污染' ">
                    <span class="text-red">{{aqiNumber}}</span>
                    <i class="text-red">{{aqiNumber}}</i>
                </template>
                <template v-else-if="aqiLevel == '重度污染' ">
                    <span class="text-purple">{{aqiNumber}}</span>
                    <i class="text-purple">{{aqiNumber}}</i>
                </template>
                <template v-else-if="aqiLevel == '严重污染' ">
                    <span class="text-brown">{{aqiNumber}}</span>
                    <i class="text-brown">{{aqiNumber}}</i>
                </template>
            </div>
            <div class="r_ctt">
                <p class="quality">
                    <span v-if="aqiLevel == '优' " class="text-green">{{aqiLevel}}</span>
                    <span v-else-if="aqiLevel == '良' " class="text-yellow">{{aqiLevel}}</span>
                    <span v-else-if="aqiLevel == '轻度污染' " class="text-orange">{{aqiLevel}}</span>
                    <span v-else-if="aqiLevel == '中度污染' " class="text-red">{{aqiLevel}}</span>
                    <span v-else-if="aqiLevel == '重度污染' " class="text-purple">{{aqiLevel}}</span>
                    <span v-else-if="aqiLevel == '严重污染' " class="text-brown">{{aqiLevel}}</span>
                </p>
                <p class="pollution">首要污染物:</p>
                <p style="font-size: 14px; text-align: center">{{pollutant}}</p>
            </div>
        </div>
        <p class="indeStyle">空气指数</p>
        <p class="release"><span>{{ realTime }} </span> 发布</p>
        <p class="ranking">市内排名<span>{{ cityRank }}</span></p>
        <p class="dataStyle">{{dataTime}}</p>

        <div class="table_box">
            <div class="table_header">
                <div class="jz">镇街</div>
                <div>
                    <p class="border_p">PM2.5</p>
                    <p>
                        <span style="border-right: 1px solid">浓度</span>
                        <span>排名</span>
                    </p>
                </div>

                <div>
                    <p class="border_p">TSP</p>
                    <p>
                        <span style="border-right: 1px solid">浓度</span>
                        <span>排名</span>
                    </p>
                </div>
            </div>
            <div class="table_body">
                <vue-seamless-scroll
                    :data="tableData"
                    class="seamless-warp"
                    :class-option="classOption"
                >
                    <a
                        href="javascript:;"
                        @click="goToDetails(item)"
                        v-for="(item, index) in tableData"
                        :key="index"
                        class="table_content"
                    >
                        <div style="width: 75px">{{ item.Street }}</div>
                        <div style="width: 40px">
                            <div v-if="item.pm_concentration<=50" class="bg-green" style="color: #000">{{ item.pm_concentration }}</div>
                            <div v-else-if="item.pm_concentration>50&&item.pm_concentration<=100" class="bg-yellow" style="color: #000">{{ item.pm_concentration }}</div>
                            <div v-else-if="item.pm_concentration>100&&item.pm_concentration<=150" class="bg-orange" style="color: #000">{{ item.pm_concentration }}</div>
                            <div v-else-if="item.pm_concentration>150&&item.pm_concentration<=200" class="bg-red" style="color: #000">{{ item.pm_concentration }}</div>
                            <div v-else-if="item.pm_concentration>200&&item.pm_concentration<=300" class="bg-purple" style="color: #000">{{ item.pm_concentration }}</div>
                            <div v-else-if="item.pm_concentration>300" class="bg-brown" style="color: #000">{{ item.pm_concentration }}</div>
                            <div v-else-if="item.pm_concentration<0" style="color: #000">--</div>
                        </div>
                        <div style="width: 37px">
                            <span v-if="item.pm_ranking<0">--</span>
                            <span v-else>{{ item.pm_ranking }}</span>
                        </div>
                        <div style="width: 40px">
                            <div v-if="item.tsp_concentration<=50" class="bg-green" style="color: #000">{{ item.tsp_concentration }}</div>
                            <div v-else-if="item.tsp_concentration>50&&item.tsp_concentration<=100" class="bg-yellow" style="color: #000">{{ item.tsp_concentration }}</div>
                            <div v-else-if="item.tsp_concentration>100&&item.tsp_concentration<=150" class="bg-orange" style="color: #000">{{ item.tsp_concentration }}</div>
                            <div v-else-if="item.tsp_concentration>150&&item.tsp_concentration<=200" class="bg-red" style="color: #000">{{ item.tsp_concentration }}</div>
                            <div v-else-if="item.tsp_concentration>200&&item.tsp_concentration<=300" class="bg-purple" style="color: #000">{{ item.tsp_concentration }}</div>
                            <div v-else-if="item.tsp_concentration>300" class="bg-brown">{{ item.tsp_concentration }}</div>
                            <div v-else-if="item.tsp_concentration<0" style="color: #000">--</div>

                        </div>
                        <div style="width: 37px">
                            <span v-if="item.tsp_ranking<0">--</span>
                            <span v-else>{{ item.tsp_ranking }}</span>
                        </div>
                    </a>
                </vue-seamless-scroll>
            </div>
        </div>
    </div>
</template>

<script>
import { http } from "@/utils/request";
export default {
    data() {
        return {
            tableData: [],
            // aqi数值
            aqiNumber:0,
            // 空气质量
            aqiLevel:'',
            // 首要污染物
            pollutant:'',
            // 发布日期
            realTime:'' ,
            // 市内排名
            cityRank:'',
            // 日期
            dataTime: '',

        };
    },
    computed: {
        classOption() {
            return {
                step: .5, // 数值越大速度滚动越快
                // limitMoveNum: 10, // 开始无缝滚动的数据量 this.dataList.length
                hoverStop: true, // 是否开启鼠标悬停stop
                direction: 1, // 0向下 1向上 2向左 3向右
                // openWatch: true, // 开启数据实时监控刷新dom
                singleHeight: 0, // 单步运动停止的高度(默认值0是无缝不停止的滚动) direction => 0/1
                singleWidth: 0, // 单步运动停止的宽度(默认值0是无缝不停止的滚动) direction => 2/3
                waitTime: 1000, // 单步运动停止的时间(默认值1000ms)
            };
        },
    },
    created() {
        // 实时空气质量
        this.getAirsDataList();
        // 街镇排名
        this.getTownsRankDataList(); 
    },
    methods: {
        // 实时空气质量
        getAirsDataList() {
            let json = {username: "api", password: "a123456b"}
            http.get("https://api-dx-smart-city.airqualitychina.cn:9998/v1/dashboard/cmd/airs", { params:{
                json
            } }).then((res) => {
                if (res.data.code === 2000) {
                    this.aqiNumber = res.data.result[1].vardata[0].value
                    this.aqiLevel = res.data.result[1].vardata[8].value
                    this.pollutant = res.data.result[1].vardata[7].value
                    this.realTime = res.data.realTime
                    this.cityRank = res.data.result[1].vardata[1].rank

                } 
            });
        },
        // 街镇排名
        getTownsRankDataList() {
            let json = {username: "api", password: "a123456b"}
            http.get("https://api-dx-smart-city.airqualitychina.cn:9998/v1/dashboard/cmd/towns", { params:{
                json
            } }).then((res) => {
                if (res.data.code === 2000) {
                    this.dataTime = res.data.data_time.slice(0, 10);
                    this.tableData = res.data.result;
                    this.tableData = this.tableData.map(item=>{
                        return {
                            Street:item.name,
                            pm_concentration:item.vardata[0].value,
                            pm_ranking:item.vardata[0].rank,
                            tsp_concentration:item.vardata.filter(ele=>{return ele.type == 'TSP'})[0].value,
                            tsp_ranking:item.vardata.filter(ele=>{return ele.type == 'TSP'})[0].rank
                        }
                    })
                } 
            });
        },
    },
};
</script>

<style lang="scss" scoped>
.table_box {
    margin-top: 10px;
    width: 230px;
    height: 200px;
    .table_header {
        width: 230px;
        height: 40px;
        font-size: 12px;
        background: linear-gradient(
            90deg,
            #4197e3 0%,
            rgba(74, 129, 169, 0) 100%
        );
        opacity: 0.87;
        div {
            width: 75px;
            height: 40px;
            text-align: center;
            border-right: 1px solid;
            float: left;
            overflow: hidden;
            p {
                height: 20px;
                line-height: 20px;

                span {
                    display: inline-block;
                    width: 37px;
                    height: 20px;
                }
            }
            .border_p {
                border-bottom: 1px solid;
            }
        }
        div:nth-child(3) {
            border-right: none;
        }
        .jz {
            line-height: 40px;
        }
    }
    .table_body {
        margin-top: 10px;
        height: 150px;
        overflow: hidden;
    }

    .table_content {
        display: block;
        height: 21px;
            line-height: 17px;
        div {
            text-align: center;
            display: inline-block;
            font-size: 9px;
        }
    }
    .table_content:nth-child(2n) {
        // background-color: #99a9bf;
        background: linear-gradient(
            to right,
            rgba(74, 129, 169, 0) 0%,
            #447ca5 50%,
            rgba(74, 129, 169, 0) 100%
        );
    }
}
.title {
    width: 197px;
        height: 46px;
        text-align: center;
        line-height: 46px;
        font-size: 21px;
        float: left;
        background:url('~@/assets/center-box-title.png');
        margin-top:11px;
}
.topbox {
    width: 100%;
    height: 100px;
    // background-color: red;
    .l_img {
        width: 94px;
        height: 91px;
        float: left;
        background: url("../../assets/l_img.png");
        position: relative;
        span {
            display: block;
            text-align: center;
            font-size: 35px;
            font-weight: 600;
            line-height: 49px;
            &.text-green{
                color: #00E600;
            }
            &.text-yellow{
                color: #ffff00;
            }
            &.text-orange{
                color: #FF7F00;
            }
            &.text-red{
                color: #ff0000;
            }
            &.text-purple{
                color: #9A004A;
            }
            &.text-brown{
                color: #800021;
            }
        }
        i {
            position: absolute;
            top: -1px;
            left: 26px;
            font-style: normal;
            text-align: center;
            font-size: 35px;
            font-weight: 600;
            line-height: 49px;
            &.text-green{
                color: #00E600;
                text-shadow: 4px -3px 0px rgba(129, 255, 194, 0.34);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
            }
            &.text-yellow{
                color: #ffff00;
                text-shadow: 4px -3px 0px rgba(255, 230, 0, 0.34);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
            }
            &.text-orange{
                color: #FF7F00;
                text-shadow: 4px -3px 0px rgba(255, 175, 2, 0.34);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
            }
            &.text-red{
                color: #ff0000;
                text-shadow: 4px -3px 0px rgba(255, 40, 2, 0.34);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
            }
            &.text-purple{
                color: #9A004A;
                text-shadow: 4px -3px 0px rgba(255, 19, 2, 0.301);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
            }
            &.text-brown{
                color: #800021;
                text-shadow: 4px -3px 0px rgba(172, 0, 0, 0.305);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
            }
        }
    }
    .r_ctt {
        width: 125px;
        height: 100px;
        float: right;
        .quality {
            font-size: 28px;
            font-family: Helvetica;
            color: #74efff;
            margin-top: 10px;
        }
        .pollution {
            width: 115px;
            font-size: 14px;
            padding-left: 1rem;
        }
    }
    .indeStyle {
        font-size: 12px;
        padding-left: 5px;
    }
    .release {
        margin-top: 6px;
        padding-left: 10px;
        width: 176px;
        height: 21px;
        background: linear-gradient(
            90deg,
            #4399e4 0%,
            rgba(24, 30, 38, 0) 100%
        );
        font-size: 10px;
        line-height: 21px;
        span {
            font-size: 14px;
            color: #91f2ff;
        }
    }
    .ranking {
        margin-top: 6px;
        padding-left: 10px;
        width: 176px;
        height: 21px;
        background: linear-gradient(
            90deg,
            #4399e4 0%,
            rgba(24, 30, 38, 0) 100%
        );
        font-size: 10px;
        line-height: 13px;
        span {
            font-size: 21px;
            color: #91f2ff;
            padding-left: 5px;
            position: relative;
            top:3px;
        }
    }
    .dataStyle {
        width: 100%;
        height: 13px;
        font-size: 9px;
        margin-top: 17px;
    }
}
</style>

