    </div>
    <!-- 比例 -->
    <div class="proportionBox">
      <div style="margin: 8px">
        <div class="top">
          <span>{{ dunValue }}</span>
          <span>(吨/平方公里)</span>
        </div>
        <div class="btm">
          <span style="margin-right: 5px">同比</span>
          <span></span>
          <span class="text-red" v-if="tongBiValue>0">{{tongBiValue}}</span>
          <span class="text-green" v-else>{{tongBiValue}}</span>
          <span v-if="tongBiValue<0" class="icon-green"></span>
          <span v-else class="icon-red"></span>
        </div>
      </div>
    </div>
    <!-- 镇街降尘量 -->
