          <span style="width: 80px; text-align: center;">镇街</span>
          <span style="line-height:25px; width:110px;">降尘量 <br /><span style="font-size:12px; white-space: nowrap;">(吨/平方公里)</span></span>
          <span style="width: 35px; text-align: center;">同比</span>
        </div>
        <div class="my-table-body">
          <vue-seamless-scroll :data="tableData" class="seamless-warp" :class-option="classOption">

            <a href="javascript:;" @click="goToDetails(item)" v-for="(item, index) in tableData" :key="index" class="my-table-content">
              <div style="width: 35px; text-align: center;">{{ index+1 }}</div>
              <div style="width: 80px; text-align: center; white-space:nowrap;">{{ item.adm_name }}</div>
              <div style="width: 55px; text-align: center;">{{ item.value }}</div>
              <!-- <div style="width: 55px; text-align: center;">-</div> -->
              <div style="width: 55px; text-align: center;">
                <span class="text-green" v-if="item.yoy_ratio <= 0&&item.yoy_ratio!=-99">{{item.yoy_ratio}}</span>
                <span class="text-red" v-else-if="item.yoy_ratio > 0">{{item.yoy_ratio}}</span>
                <span v-else-if="item.yoy_ratio==-99">-</span>
              </div>
              <div>
                <span class="icon-green" v-if="item.yoy_ratio <= 0&&item.yoy_ratio!=-99"></span>
                <span class="icon-red" v-else-if="item.yoy_ratio > 0"></span>
                <span v-else-if="item.yoy_ratio < 0"></span>
              </div>
            </a>
          </vue-seamless-scroll>
        </div>
      </div>
    </div>
