<script>
import { http } from "@/utils/request";
export default {
  data() {
    return {
      // 吨
      tongBiValue: 0,
      // 同比
      dunValue: 0,
      tableData: [
        // {
        //     sort: 1,
        //     Street: '清源街道',
        //     heavy: '60',
        //     proportion: '6.6%',
        //     status: 0,
        // },
        // {
