        // },
        // {
        //     sort: 3,
        //     Street: '清源街道',
        //     heavy: '20',
        //     proportion: '9.6%',
        //     status: 1,
        // },
        // {
        //     sort: 4,
        //     Street: '清源街道',
        //     heavy: '20',
        //     proportion: '9.6%',
        //     status: 1,
        // },
        // {
        //     sort: 5,
        //     Street: '清源街道',
        //     heavy: '20',
        //     proportion: '9.6%',
        //     status: 1,
        // },
        // {
        //     sort: 6,
        //     Street: '清源街道',
        //     heavy: '60',
        //     proportion: '6.6%',
        //     status: 0,
        // }
      ],
    };
  },
  computed: {
    classOption() {
      return {
        step: 0.5, // 数值越大速度滚动越快
        // limitMoveNum: 10, // 开始无缝滚动的数据量 this.dataList.length
        hoverStop: true, // 是否开启鼠标悬停stop
        direction: 1, // 0向下 1向上 2向左 3向右
        // openWatch: true, // 开启数据实时监控刷新dom
        singleHeight: 0, // 单步运动停止的高度(默认值0是无缝不停止的滚动) direction => 0/1
        singleWidth: 0, // 单步运动停止的宽度(默认值0是无缝不停止的滚动) direction => 2/3
        waitTime: 1000, // 单步运动停止的时间(默认值1000ms)
      };
    },
  },
  created() {
    // 镇街降尘量
    this.getDustsDataList();
  },
  methods: {
    // 镇街降尘量
    getDustsDataList() {
      let json = {
        username: "api",
        password: "a123456b",
      };
      http
        .get(
          "https://api-dx-smart-city.airqualitychina.cn:9998/v1/dashboard/cmd/dust-rank",
          {
            params: {
              json,
            },
          }
        )
        .then((res) => {
          if (res.data.code === 2000) {
            this.tableData = [...res.data.result];
            this.tableData.pop();
            this.dunValue = res.data.result[res.data.result.length - 1].value;
            this.tongBiValue =
              res.data.result[res.data.result.length - 1].yoy_ratio;
              console.log(this.tongBiValue)

            // this.tableData.forEach(item=>{
            //     if(item.adm_name == '全区'){
            //         item.value = +String(item.value).slice(0,4)
            //     }
            // })
          }
        });
    },
  },
};
