<template>
    <div style="padding-top:10px;">
        <div class="title">水质断面月度情况</div>
        <div class="table-box">
            <div class="my-table-header">
                <!-- <span  style="width: 40px; text-align: center;">序号</span> -->
                <span  style="width: 90px; text-align: center;">断面名称</span>
                <span style="width: 70px; text-align: center;">水质现状</span>
                <span style="width: 70px; text-align: center;">是否达标</span>
            </div>
            <div class="my-table-body">
                <vue-seamless-scroll :data="tableData" class="seamless-warp"  :class-option="classOption">

                <a
                    href="javascript:;"
                    v-for="(item, index) in tableData"
                    :key="index"
                    class="my-table-content"
                >
                    <!-- <div class="my_currentPage" style="width: 40px;">{{ index+1 }}</div> -->
                    <div class="my_currentPage" style="width: 90px; overflow: hidden;text-overflow:ellipsis;white-space: nowrap;">{{ item.name }}</div>
                    <div class="my_currentPage" style="width: 60px;">{{ item.check }}</div>
                    <div class="my_currentPage" style="width: 65px;">达标</div>
                </a>
                </vue-seamless-scroll>
            </div>
        </div>
    </div>
</template>

<script>
import {http} from '@/utils/request'
export default {
    data() {
        return {
            tableData: [],
        }
    },
    created() {
        this.requestTableData()
    },
    methods: {
        requestTableData() {
            let json = {
                username: "api", 
                password: "a123456b"
            }
            http.get('https://api-dx-smart-city.airqualitychina.cn:9998/v1/dashboard/water/sections', {params: {json}})
            .then(res => {
                this.tableData = res.data.result;
                this.tableData = this.tableData.filter(item=>{
                    return item.type == '国考断面' || item.type == '市考断面'
                })
            })
        },
    },
    computed:{
        classOption() {
            return {
            step: .5, // 数值越大速度滚动越快
            limitMoveNum: 10, // 开始无缝滚动的数据量 this.dataList.length
            hoverStop: true, // 是否开启鼠标悬停stop
            direction: 1, // 0向下 1向上 2向左 3向右
            // openWatch: true, // 开启数据实时监控刷新dom
            singleHeight: 0, // 单步运动停止的高度(默认值0是无缝不停止的滚动) direction => 0/1
            singleWidth: 0, // 单步运动停止的宽度(默认值0是无缝不停止的滚动) direction => 2/3
            waitTime: 1000, // 单步运动停止的时间(默认值1000ms)
            };
        },
    }
}
</script>


<style lang="scss" scoped>
@import '../../styles/monthly.scss';
.title {
    width: 197px;
        height: 46px;
        text-align: center;
        line-height: 46px;
        font-size: 21px;
        background:url('~@/assets/center-box-title.png');
}
</style>