<template>
    <div class="hover-echarts-container" >
        <el-dialog
            :title="echartsData.name"
            :visible.sync="dialogVisible"
            width="80%"
        >
            <div class="content">更新时间 : {{echartsData.data[0].values[echartsData.data[0].values.length-1].qc_datetime}}</div>
            <div class="content">点位类型 : {{echartsData.devsite_prop_name}}
                <span style="margin-left:20px;">属地 : {{echartsData.admin_county_name}}/{{echartsData.admin_town_name}}</span>
            </div>
            <div class="content">地址 : {{echartsData.address}}</div>
            
            <div class="number-box">
                <div :class="pmClass">{{pmValue > 0 ? pmValue : '-' }}</div>
                <div>PM2.5</div>
            </div>
            
            <BarEcharts style="margin-left:0px;margin-top:20px;" :echartsData="echartsData.data[0].values.map((item)=>{
                return item.qc_value
            })"
                :xAxisTitle="ndChangeEchartsDataYAxisTitle"
                :echartTitle="{
                        text:'PM2.5(μg/m³)',
                        left:'100px'
                    }"
             />
            <!-- <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible = false"
                    >确 定</el-button
                >
            </span> -->
        </el-dialog>
    </div>
</template>

<script>
import BarEcharts from '@/components/Echarts/Bar'
export default {
    components: {
        BarEcharts
    },
    data(){
        return {
            dialogVisible:false,
            echartsData:[],
            ndChangeEchartsDataYAxisTitle:[]
        }
    },
    computed:{
        pmValue(){
            return this.echartsData.data[0].values[this.echartsData.data[0].values.length-1].qc_value
        },
        pmClass(){
            if((this.pmValue>0&&this.pmValue<=50)|| this.pmValue == '-'){
                    return 'bg-green'
                }else if(this.pmValue>50&&this.pmValue<=100){
                    return 'bg-yellow'
                }else if(this.pmValue>100&&this.pmValue<=150){
                    return 'bg-orange'
                }else if(this.pmValue>150&&this.pmValue<=200){
                    return 'bg-red'
                }else if(this.pmValue>200&&this.pmValue<=300){
                    return 'bg-purple'
                }else if(this.pmValue>300){
                    return 'bg-brown'
                }
        }
    },
    props: ["echartsDataHover"],
    watch:{
        echartsDataHover(newVal){
            this.echartsData = newVal
            this.ndChangeEchartsDataYAxisTitle = this.echartsData.data[0].values.map(item=>{
                return item.qc_datetime.slice(5,16)
            })
            this.ndChangeEchartsDataYAxisTitle.forEach((item,index)=>{
                        if(index%4 !==1 ){
                            this.ndChangeEchartsDataYAxisTitle[index] = ''
                        }
                    })
        }
    },
    methods:{
        init(data){
            this.dialogVisible = true
            this.echartsData = data
            this.ndChangeEchartsDataYAxisTitle = this.echartsData.data[0].values.map(item=>{
                return item.qc_datetime.slice(5,16)
            })
            this.ndChangeEchartsDataYAxisTitle.forEach((item,index)=>{
                        if(index%4 !==1 ){
                            this.ndChangeEchartsDataYAxisTitle[index] = ''
                        }
                    })
        }
    }
};
</script>

<style lang="scss">
.hover-echarts-container{
    .el-dialog{
        background:#19326d;
        .el-dialog__header{
            line-height:31px;
background: linear-gradient(
90deg, #42a7e7 0%, rgba(24, 30, 38, 0) 100%);
        }
        
        .el-dialog__title{
            color:#fff;
            font-size:25px;
        }
        .el-dialog__headerbtn .el-dialog__close{
            color:#fff;
        }
    }
    .content{
        color:#fff;
        line-height: 30px;
    }
    .number-box{
        margin-top:20px;
        div{
            width:80px;
            height:30px;
            line-height:30px;
            text-align: center;
            background:#70ec49;
            color:#333;
            font-size:18px;
            box-sizing: border-box;
            &.bg-green {
                background: #00E600;
            }
            &.bg-yellow {
                background: #ffff00;
            }
            &.bg-orange {
                background: #ff7f00;
            }
            &.bg-red {
                background: #ff0000;
            }
            &.bg-purple {
                background: #9a004a;
            }
            &.bg-brown {
                background: #800021;
            }

        }
        div:nth-of-type(2){
            color:#fff;
background:#3c4a76;
        }
    }
}
</style>