    methods:{
        // 镇街道路积尘负荷
        getDustsDataList() {
            let json = {
                username: "api",
                 password: "a123456b",
            }
            http.get("https://api-dx-smart-city.airqualitychina.cn:9998/v1/dashboard/cmd/dusts", { params:{
                json
            } }).then((res) => {
                if (res.data.code === 2000) {
                    this.tableData = res.data.result
                    this.tableData.forEach(item=>{
                        if(item.name == '全区'){
                            item.avg_dust = +String(item.avg_dust).slice(0,4)
                        }
                    })  
                } 
            });
        }
    }
