<template>
    <div class="bar-chart-container">
        <!-- 为 ECharts 准备一个具备大小（宽高）的 DOM -->
        <div id="main" :style="{ width, height }"></div>
    </div>
</template>

<script>
import * as echarts from "echarts";
export default {
    props: {
        width: {
            type: String,
            default: "100%",
        },
        height: {
            type: String,
            default: "300px",
        },
        // 累计积分
        echartsData: {
            type: Array,
            default: () => [0],
        },
        // 累计上报条数
        totalReportNumberData: {
            type: Array,
            default: () => [],
        },
        // 累计上报积分
        totalReportScoreData: {
            type: Array,
            default: () => [],
        },
        // x轴坐标title
        xAxisTitle: {
            type: Array,
            default: () => [0],
        },
        // title
        echartTitle: {
            type: Object,
            default: {
                text: "",
                left: "center",
            },
        },
    },
    data() {
        return {
            myChart: null,
            myOptions: {
                xAxis: {
                    minInterval:1,
                    data: [],
                    axisLabel: {
                        color: '#fff',
                        fontSize: 14,//如果全部显示不开,可以选择字体大小
                        interval:0 ,  //控制是否全部显示
                        // rotate: 30//控制字体倾斜
                    },
                    splitLine: {
                        show: false,
                    },
                },
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'shadow'
                    },
                    toolbox: {
                        feature: {
                        dataView: { show: true, readOnly: true },
                        magicType: { show: true, type: ['line', 'bar'] },
                        restore: { show: true },
                        saveAsImage: { show: true }
                        }
                    },
                },
                yAxis: {
                    type: "value",
                    minInterval:1,
                    min:0,
                    axisLabel: {
                        color: "#fff",
                        fontSize: 14, //如果全部显示不开,可以选择字体大小
                        // interval:0 ,  //控制是否全部显示
                        // rotate: 20//控制字体倾斜
                    },
                    splitLine: {
                        show:false,
                    }
                },
                title: {
                    text: "",
                    left: 'center',
                    textStyle:{
                        color:'#fff',
                        fontSize: 10,
                    }
                },
                series: [
                    {
                        type: "bar",
                        data: [],
                        showBackground: true,
                        itemStyle: {
                            color: function (params) {
                                if(params.value>0 && params.value <=50){
                                    return '#00e600'
                                }else if(params.value>50 && params.value <=100){
                                    return '#ffff00'
                                }else if(params.value>100 && params.value <=150){
                                    return '#ff7f00'
                                }else if(params.value>150 && params.value <=200){
                                    return '#ff0000'
                                }else if(params.value>200 && params.value <=300){
                                    return '#9a004a'
                                }else if(params.value>300){
                                    return '#800021'
                                }
                                // let colorlist = ['#5470c6', '#91cc75', '#fac858', '#ee6666', '#73c0de', '#3ba272', '#fc8452', '#9a60b4', '#ea7ccc'];
                                // let colorlist = ['#5470c6', '#91cc75', '#fac858', '#ee6666', '#73c0de', '#3ba272', '#fc8452', '#9a60b4', '#ea7ccc',
                                // '#5470c6', '#91cc75', '#fac858', '#ee6666', '#73c0de', '#3ba272', '#fc8452', '#9a60b4', '#ea7ccc',
                                // '#5470c6', '#91cc75', '#fac858', '#ee6666', '#73c0de', '#3ba272', '#fc8452', '#9a60b4', '#ea7ccc',
                                // '#5470c6', '#91cc75', '#fac858', '#ee6666', '#73c0de', '#3ba272', '#fc8452', '#9a60b4', '#ea7ccc',
                                // '#5470c6', '#91cc75', '#fac858', '#ee6666', '#73c0de', '#3ba272', '#fc8452', '#9a60b4', '#ea7ccc',
                                // '#5470c6', '#91cc75', '#fac858', '#ee6666', '#73c0de', '#3ba272', '#fc8452', '#9a60b4', '#ea7ccc',
                                // '#5470c6', '#91cc75', '#fac858', '#ee6666', '#73c0de', '#3ba272', '#fc8452', '#9a60b4', '#ea7ccc',
                                // ];
                                
                                // return colorlist[params.dataIndex];
                            },
                        },
                    },
                ],
            },
        };
    },
    mounted() {
        // 初始化图表
        this.initCharts();
    },
    updated(){
        // 初始化图表
        this.initCharts();
    },
    watch:{
        'echartsData':{
            handler(newValue,oldValue){
                if(newValue !== oldValue){
                    this.initCharts();
                }
            },
            deep:true
        },
    },
    methods: {
        // 初始化图表
        initCharts() {
            this.myChart = echarts.init(document.getElementById("main"));
            this.myChart.setOption(this.myOptions);
            this.myChart.setOption({
                xAxis: {
                    data: this.xAxisTitle,
                },
                title: {
                    text: this.echartTitle.text,
                    left: this.echartTitle.left,
                },
                series: [
                    {
                        name:this.echartTitle.text,
                        type: "bar",
                        data: this.echartsData,
                    },
                    {
                        name:'累计上报条数',
                        type: 'line',
                        data: this.totalReportNumberData,
                    },
                    {
                        name:'累计上报积分',
                        type: 'line',
                        data: this.totalReportScoreData,
                    },
                ],
            });
            window.addEventListener("resize", this.onResize);
        },
        onResize() {
            this.myChart.resize();
        },
    },
};
</script>

<style scoped>
.bar-chart-container{
    /* padding-bottom:100px; */
}

</style>